.messageSender
{
    display:flex;
    margin-top:30px;
    flex-direction:column;
    background-color:#fff;
    border-radius:15px;
    box-shadow:0px 5px 7px -7px rgba(0,0,0,0.75);
    width:100%;
}

.messageSender__top
{
    display:flex;
    border-bottom:1px solid #fff;
    padding:15px;
}

.messageSender__top > form
{
    display:flex;
   flex:1;
}
.messageSender__top > form > input
{
    outline-width:0;
    border:none;
    padding:5px 20px;
    margin:0 10px;
    border-radius:999px;
    background-color:#ccc;
}

.messageSender__top > form > button
{
  display:none;
}

.messageSender_input
{flex:1}
.messageSender__bottom
{
    display: flex;
    justify-content: space-evenly;
}

.messageSender__option
{
    padding:20px;
    display:flex;
    align-items: center;
    color:gray;
    margin:5px;
}

.messageSender__option > h3
{
   font-size: medium;
   margin-left:10px;
   cursor: pointer;
}

.messageSender__option:hover
{
   background-color: #c6d6de;
  border-radius: 20px;
}